import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Notification } from '../utils/notification.util';
import { UserService } from '../services/user.service';
import { Router } from '@angular/router';
import { ShowHideUiService } from '../services/ShowHideUi.service';


// import { MongoDBService } from '../Services/MongoDB.service';
// import { Customer } from './Customer';

@Component({
    selector: 'app-user-profile',
    templateUrl: './user-profile.component.html',
    styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent implements OnInit {
    
    // user = {
    //   fullName: String,
    //   email: String,
    //   password: String,
    //   birthDate: String,
    //   phoneNumber: String,
    //   projectName: String,
    //   companyName: String
    // };
    user: any = {};
    isAdmin: boolean = localStorage.getItem('type') == 'admin' ? true : false;
    
    constructor(
        private userService: UserService,
        private router: Router,
        private showHide: ShowHideUiService
    ) { }
    
    ngOnInit() {
        this.showHide.currentUser.subscribe(
            res => {
                console.log(res);
                if(res.type == 'admin' || this.isAdmin) {
                    this.isAdmin = true;
                } else if(res.type == ''){
                    this.isAdmin = undefined;
                }
            },
            error => {
                console.log(error);
                if(error.status == 401) {
                    localStorage.clear();
                    Notification.showNotification('top', 'right', 'info', 'Session timeout!');
                }
            }
        )
        if(this.isAdmin == undefined) {
            Notification.showNotification('top', 'right', 'info', 'Log in please!');
            this.router.navigate(['/login']);
        } else if(!this.isAdmin){
            Notification.showNotification('top', 'right', 'warning', 'You can not add a user!');
        }
    }
    
    addUser(form: NgForm) {
        if (form.valid) {
            console.log(form.value);
            this.userService.addUser(form.value)
            .subscribe(
                res => {
                    console.log(res, undefined, 2);
                    Notification.showNotification('top', 'right', 'success', `${form.value.type} created!`);
                    form.resetForm();
                },
                error => {
                    console.log(error, undefined, 2);
                    Notification.showNotification('top', 'right', 'warning', 'Try again!');
                }
            );
        } else {
            console.log(`Value: ${form.value}`);
            Notification.showNotification('top', 'right', 'warning', 'All fields are mandatory!');
        }
    }
    
}



