import { Component, OnInit, Output } from '@angular/core';
import { AuthService } from '../services/auth.service';
import { ShowHideUiService } from '../services/ShowHideUi.service';
import { Notification } from '../utils/notification.util';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { EventEmitter } from 'events';

@Component ({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
    
    user: any = {}
    @Output() signedIn = new EventEmitter();
    
    constructor(
        private authentication: AuthService,
        private router: Router,
        private showHide: ShowHideUiService
    ) {
        localStorage.clear();
    }
    
    ngOnInit() {
    }
    
    login(form: NgForm) {
        if (form.valid) {
            this.authentication.signIn(form.value)
            .subscribe(
                res => {
                    var currentUser = {name: res.fullName, type: form.value.type};
                    localStorage.setItem('username', currentUser.name);
                    localStorage.setItem('type', currentUser.type);
                    localStorage.setItem('token', res.token);
                    localStorage.setItem('email', form.value.email);
                    // console.log(currentUser);
                    this.showHide.setLoggedInUser(currentUser);
                    this.router.navigate(['/dashboard']);
                    Notification.showNotification('top', 'right', 'success', 'Logged In!');
                },
                error => {
                    console.log(JSON.stringify(error, undefined, 2));
                    Notification.showNotification('top', 'right', 'danger', 'Login failed!');
                }
            )
        } else {
            Notification.showNotification('top', 'right', 'warning', 'Fill in both fields!');
        }
    }
    
}



