import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Notification } from '../utils/notification.util';
import { UserService } from '../services/user.service';
import { Router } from '@angular/router';
import { ShowHideUiService } from '../services/ShowHideUi.service';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { NgForm } from '@angular/forms';

@Component({
    selector: 'app-table-list',
    templateUrl: './table-list.component.html',
    styleUrls: ['./table-list.component.css']
})
export class TableListComponent implements OnInit {
    
    user: any = {};
    isAdmin: boolean = localStorage.getItem('type') == 'admin' ? true : false;
    customers:any [];
    managers: any [];
    customersInstances: any [];
    managersInstances: any [];
    _id: string;    
    closeResult: string;
    
    constructor(
        private userService: UserService,
        private router: Router,
        private showHide: ShowHideUiService,
        private modalService: NgbModal
    ) { }
    
    ngOnInit() {
        this.showHide.currentUser.subscribe(
            res => {
                if(res.type == 'admin' || this.isAdmin) {
                    this.isAdmin = true;
                    this.getAllCustomers();
                } else if(res.type == ''){
                    this.isAdmin = undefined;
                }
            },
            error => {
                if(error.status == 401) {
                    localStorage.clear();
                    Notification.showNotification('top', 'right', 'info', 'Session timeout!');
                }
            }
        )
        if(typeof this.isAdmin == 'undefined') {
            Notification.showNotification('top', 'right', 'info', 'Log in please!');
            this.router.navigate(['/login']);
        } else if(!this.isAdmin){
            Notification.showNotification('top', 'right', 'warning', 'You can not view customers!');
        }
    }
    
    getAllCustomers() {
        this.userService.getAllData()
        .subscribe(
            res => {
                this.customers = res.customers;
                this.managers = res.managers;
                this.customersInstances = res.customersInstances;
                this.managersInstances = res.managersInstances;
            },
            error => {
                Notification.showNotification('top', 'right', 'info', 'If data does not update automatically, please reload the page!');
                // this.router.navigate(['/login']);
            }
        );
    }
    
    editRecord(form: NgForm) {
        if (form.valid) {
            this.user.newEmail = this.user.email;
            this.user.newPassword = this.user.password;
            console.log(this.user);
            this.userService.updateUser(this.user)
            .subscribe(
                res => {
                    // console.log(res, undefined, 2);
                    Notification.showNotification('top', 'right', 'success', `${this.user.type} updated!`);
                    this.ngOnInit();
                },
                error => {
                    Notification.showNotification('top', 'right', 'warning', `${this.user.type} ${error.statusText}`);
                }
            );
        } else {
            console.log(`Value: ${form.value}`);
            Notification.showNotification('top', 'right', 'warning', 'All fields are mandatory!');
        }
    }
    
    deleteRecord(user: any) {
        let answer = prompt("This record will be deleted from database!\nType email id to continue.");
        // console.log(this.customers.indexOf(user));
        // console.log(answer);
        // console.log(user.email);
        // console.log(answer == user.email);
        
        if(answer == user.email) {
            
            if(user.type == 'customer') {
                console.log(this.managers);
                var customers: any [];
                for(let i = 0; i<this.managers.length; i++) {
                    customers.push(this.managers[i].customers.splice(this.managers[i].customers.indexOf(user.email), 1));
                }
                console.log(customers);
            }
            
            // this.customers.splice(this.customers.indexOf(user._id)+2, 1);
            // var body = {email: user.email, type: user.type};
            // this.userService.deleteUser(body)
            // .subscribe(
            //     res => {
            //         // console.log(res, undefined, 2);
            //         Notification.showNotification('top', 'right', 'success', `${user.type} deleted!`);
            //         this.ngOnInit();
            //     },
            //     error => {
            //         // console.log(error, undefined, 2);
            //         Notification.showNotification('top', 'right', 'warning', 'Try again!');
            //     }
            // );
        }
    }
    
    open(content, user) {
        this._id = user._id;
        this.user = user;
        this.user.birthDate = user.birthDate.split('T')[0];
        
        this.modalService.open(content, { centered: true }).result.then((result) => {
            this.closeResult = `Closed with: ${result}`;
        }, (reason) => {
            this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
        });
    }
    
    private getDismissReason(reason: any): string {
        if (reason === ModalDismissReasons.ESC) {
            return 'by pressing ESC';
        } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
            return 'by clicking on a backdrop';
        } else {
            return  `with: ${reason}`;
        }
    }
    
}



