import { Component, OnInit } from '@angular/core';
import { UserService } from '../services/user.service';
import { Notification } from '../utils/notification.util';
import { Router } from '@angular/router';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { NgForm } from '@angular/forms';
import { AuthService } from '../services/auth.service';

import * as _ from "lodash";

@Component({
    selector: 'app-dashboard',
    templateUrl: './dashboard.component.html',
    styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
    
    user: any = {
        instances: ''
    };
    numberOfCustomers: number;
    numberOfManagers: number;
    customers:any [];
    managers:any [];
    customersInstances:any [];
    managersInstances:any [];
    _id: string;    
    closeResult: string;
    isAdmin: boolean = localStorage.getItem('type') == 'admin' ? true : false;
    isCustomer: boolean = localStorage.getItem('type') == 'customer' ? true : false;
    isManager: boolean = localStorage.getItem('type') == 'manager' ? true : false; 
    
    customersInManager: any = [];
    
    instanceId: any;
    
    render: boolean = false;
    renderManager: boolean = false;
    
    constructor(
        private userService: UserService,
        private router: Router,
        private modalService: NgbModal
    ) { 
        if(localStorage.getItem('token') == null) {
            router.navigate(['/login']);
            Notification.showNotification('top', 'right', 'info', 'Login in please!');
            localStorage.clear();
        }
        
        var body = {
            apiVersion: "2016-11-15"
        }
        
        if(this.isCustomer) {
            let body = {
                email: localStorage.getItem('email'),
                type: 'customer'
            }
            // console.log(body);
            this.getSpecificUserData(body);
            return;
        }        
        
        
        
        
        
        
        
        
        if(this.isManager) {
            let body = {
                email: localStorage.getItem('email'),
                type: 'manager'
            }
            // console.log(body);
            this.customers = [];
            this.customersInstances = [];
            this.userService.getUserData(body)
            .subscribe(
                res => {
                    // console.log(JSON.stringify(res, undefined, 2));
                    for(let temp = 0; temp<res.customers.length; temp++) {
                        var body2 = {
                            email: res.customers[temp],
                            type: 'customer'
                        }
                        this.userService.getUserData(body2)
                        .subscribe(
                            res => {
                                this.customers.push(res);
                                this.customersInstances += res.instances.length;
                                // this.customersInstances.push(res.instances);
                            },
                            error => {
                                console.log(error);
                            }
                        )
                    }
                    this.managers = [res];
                    this.numberOfCustomers = this.managers[0].customers.length;
                    
                    this.customers = Object.keys(this.customers).map(function(key) {
                        return [Number(key), this.customers[key]];
                    });
                    console.log(typeof this.customers);
                    
                    for(let i = 0; i<this.numberOfCustomers; i++) {
                        for(let j = 0; j<this.customers[i].instances.length; j++) {
                            console.log(this.customers);
                            // console.log(this.customers[i].instances[j]);
                            this.userService.getInstancesDescription({
                                apiVersion: "2016-11-15",
                                instanceIDs: [ this.customers[i].instances[j].instanceId ],
                                region: this.customers[i].instances[j].region
                            })
                            .subscribe(
                                res => {
                                    // console.log("Here: "+res);
                                    // console.log("Here: "+JSON.stringify(this.customers[i].instances[j]));
                                    // console.log("Here: "+JSON.stringify(this.customers[i].instances[j].INSTANCEDETAILS, undefined, 2));
                                    this.customers[i].instances[j].INSTANCEDETAILS = res;
                                    if(res.statusCode != 400) {
                                        this.render = true;
                                    } else {
                                        Notification.showNotification('top', 'right', 'info', res.message);
                                    }
                                    // console.log("Render value: "+this.render);
                                    // console.log(this.customers[i].instances[j].INSTANCEDETAILS);
                                },
                                error => {
                                    console.log(error);
                                }
                            )
                        }
                    }
                    
                    for(let j = 0; j<this.managers[0].instances.length; j++) {
                        // console.log(this.customers[0].instances[j]);
                        this.userService.getInstancesDescription({
                            apiVersion: "2016-11-15",
                            instanceIDs: [ this.managers[0].instances[j].instanceId ],
                            region: this.managers[0].instances[j].region
                        })
                        .subscribe(
                            res => {
                                // console.log("Here: "+res);
                                // console.log("Here: "+JSON.stringify(this.managers[0].instances[j]));
                                // console.log("Here: "+JSON.stringify(this.managers[0].instances[j].INSTANCEDETAILS, undefined, 2));
                                this.managers[0].instances[j].INSTANCEDETAILS = res;
                                if(res.statusCode != 400) {
                                    this.renderManager = true;
                                } else {
                                    Notification.showNotification('top', 'right', 'info', res.message);
                                }
                                // console.log("Render value: "+this.renderManager);
                                // console.log("Here: "+JSON.stringify(this.managers[0].instances[j].INSTANCEDETAILS, undefined, 2));
                            },
                            error => {
                                console.log(error);
                            }
                        )
                    }
                    
                },
                error => {
                    if(error.status == 401) {
                        localStorage.clear();
                        Notification.showNotification('top', 'right', 'info', 'Session timeout!');
                    }
                    this.router.navigate(['/login']);
                }
            );
            return;
        }
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        this.userService.getAllData()
        .subscribe(
            res => {
                // console.log(JSON.stringify(res, undefined, 2));
                this.customers = res.customers;
                this.managers = res.managers;
                this.numberOfCustomers = res.customers.length;
                this.numberOfManagers = res.managers.length;
                this.customersInstances = res.customers.instances;
                this.managersInstances = res.managers.instances;
                
                for(let i = 0; i<this.numberOfCustomers; i++) {
                    for(let j = 0; j<this.customers[i].instances.length; j++) {
                        // console.log(this.customers[i].instances[j]);
                        this.userService.getInstancesDescription({
                            apiVersion: "2016-11-15",
                            instanceIDs: [ this.customers[i].instances[j].instanceId ],
                            region: this.customers[i].instances[j].region
                        })
                        .subscribe(
                            res => {
                                // console.log("Here: "+res);
                                // console.log("Here: "+JSON.stringify(this.customers[i].instances[j]));
                                // console.log("Here: "+JSON.stringify(this.customers[i].instances[j].INSTANCEDETAILS, undefined, 2));
                                this.customers[i].instances[j].INSTANCEDETAILS = res;
                                if(res.statusCode != 400) {
                                    this.render = true;
                                } else {
                                    Notification.showNotification('top', 'right', 'info', res.message);
                                }
                                // console.log("Render value: "+this.render);
                                // console.log(this.customers[i].instances[j].INSTANCEDETAILS);
                            },
                            error => {
                                console.log(error);
                            }
                        )
                    }
                }
                
                for(let i = 0; i<this.numberOfManagers; i++) {
                    for(let j = 0; j<this.managers[i].instances.length; j++) {
                        // console.log(this.customers[i].instances[j]);
                        this.userService.getInstancesDescription({
                            apiVersion: "2016-11-15",
                            instanceIDs: [ this.managers[i].instances[j].instanceId ],
                            region: this.managers[i].instances[j].region
                        })
                        .subscribe(
                            res => {
                                // console.log("Here: "+res);
                                // console.log("Here: "+JSON.stringify(this.managers[i].instances[j]));
                                // console.log("Here: "+JSON.stringify(this.managers[i].instances[j].INSTANCEDETAILS, undefined, 2));
                                this.managers[i].instances[j].INSTANCEDETAILS = res;
                                if(res.statusCode != 400) {
                                    this.renderManager = true;
                                } else {
                                    Notification.showNotification('top', 'right', 'info', res.message);
                                }
                                // console.log("Render value: "+this.renderManager);
                                // console.log("Here: "+JSON.stringify(this.managers[i].instances[j].INSTANCEDETAILS, undefined, 2));
                            },
                            error => {
                                console.log(error);
                            }
                        )
                    }
                }
                
            },
            error => {
                if(error.status == 401) {
                    localStorage.clear();
                    Notification.showNotification('top', 'right', 'info', 'Session timeout!');
                    this.router.navigate(['/login']);
                }
            }
        );
    }
    
    
    
    
    
    
    
    getSpecificUserData(body) {
        this.userService.getUserData(body)
        .subscribe(
            res => {
                this.customers = [res];
                this.customersInstances = res.instances;
                // console.log(res);
                
                for(let j = 0; j<this.customers[0].instances.length; j++) {
                    // console.log(this.customers[0].instances[j]);
                    this.userService.getInstancesDescription({
                        apiVersion: "2016-11-15",
                        instanceIDs: [ this.customers[0].instances[j].instanceId ],
                        region: this.customers[0].instances[j].region
                    })
                    .subscribe(
                        res => {
                            this.customers[0].instances[j].INSTANCEDETAILS = res;
                            if(res.statusCode != 400) {
                                this.render = true;
                            } else {
                                Notification.showNotification('top', 'right', 'info', res.message);
                            }
                        },
                        error => {
                            console.log(error);
                        }
                    )
                }
                
            },
            error => {
                console.log(error);
            }
        )
    }
    
    
    
    
    
    
    
    
    ngOnInit() {
        
    }
    
    startInstance(instanceId, region, field) {
        let message;
        if(instanceId.trim() == "") {
            message = "Instance ID must be provided";
            return Notification.showNotification('top', 'right', 'info', message);
        } else if (region.trim() == "") {
            message = "Region must be provided";
            return Notification.showNotification('top', 'right', 'info', message);
        }
        let body = {
            type: localStorage.getItem('type'),
            instanceIDs: [instanceId],
            apiVersion: "2016-11-15",
            region: region,
            DryRun: false
        }
        this.userService.startInstance(body)
        .subscribe(
            res => {
                console.log(res);
                message = res.message == undefined ? "Starting instance!" : res.message;
                Notification.showNotification('top', 'right', 'info', message);
                this.ngOnInit();
            },
            error => {
                console.log(error);
            }
        )
    }
    
    stopInstance(instanceId, region) {
        let message;
        if(instanceId.trim() == "") {
            message = "Instance ID must be provided";
            return Notification.showNotification('top', 'right', 'info', message);
        } else if (region.trim() == "") {
            message = "Region must be provided";
            return Notification.showNotification('top', 'right', 'info', message);
        }
        let body = {
            type: localStorage.getItem('type'),
            instanceIDs: [instanceId],
            apiVersion: "2016-11-15",
            region: region,
            DryRun: false
        }
        this.userService.stopInstance(body)
        .subscribe(
            res => {
                console.log(res);
                // console.log(res.StoppingInstances[0].PreviousState.Name);
                message = res.message == undefined ? res.StoppingInstances[0].PreviousState.Name == "stopped" ? "Instance already stopped": "Stopping instance" : res.message;
                
                Notification.showNotification('top', 'right', 'info', message);
                this.ngOnInit();
            },
            error => {
                console.log(error);
            }
        )
    }
    
    updateInstances(form: NgForm, user: any) {
        for(let i = 0; i<user.instances.length; i++ ) {
            // if(user.instances[i].instanceId == undefined || user.instances[i].region == undefined || user.instances[i].instanceId.trim() == "" || user.instances[i].region.trim() == "") {
            if(user.instances[i].instanceId == undefined || user.instances[i].instanceId == null || user.instances[i].instanceId.trim() == "") {
                user.instances.splice(i, 1);
            }
        }
        var body = {
            email: user.email,
            instances: user.instances,
            type: user.type
        }
        // console.log(JSON.stringify(body, undefined, 2));
        
        var body2 = _.chain(body.instances).groupBy("region").map((v, i) => {
            return {
                region: i,
                details: _.groupBy(v, "state")
            }
        }).map((w, j) => {
            return {
                region: w.region,
                details: {
                    start: { InstanceIds: _.map(w.details.start, 'instanceId') },
                    stop: { InstanceIds: _.map(w.details.stop, 'instanceId') }
                }
            }
        }).value();
        let regionStart: any = [];
        let regionStop: any = [];
        for(let i = 0; i < body2.length; i++) {
            // console.log(JSON.stringify(body2[i].details.stop.InstanceIds, undefined, 2));
            regionStart.push({
                command: 'start',
                region: body2[i].region,
                instanceIDs: body2[i].details.start.InstanceIds,
                apiVersion: "2016-11-15",
                DryRun: false
                
            });
            regionStop.push({
                command: 'stop',
                region: body2[i].region,
                instanceIDs: body2[i].details.stop.InstanceIds,
                apiVersion: "2016-11-15",
                DryRun: false
            })
        }
        // console.log(JSON.stringify(body2, undefined, 2));
        console.log(JSON.stringify(regionStart, undefined, 2));
        console.log(JSON.stringify(regionStop, undefined, 2));
        
        let startFlag: boolean = false;
        let stopFlag: boolean = false;
        
        regionStart.forEach(region => {
            this.userService.startUserInstances(region)
            .subscribe(
                res => {
                    console.log(res);
                    startFlag = true;
                    // this.ngOnInit();
                },
                error => {
                    startFlag = false;
                    console.log(error);
                }
            )
        });
        regionStop.forEach(region => {
            this.userService.stopUserInstances(region)
            .subscribe(
                res => {
                    // Notification.showNotification('top', 'right', 'success', 'Instances updated successfully!');
                    console.log(res);
                    stopFlag = true;
                    // this.ngOnInit();
                },
                error => {
                    // Notification.showNotification('top', 'right', 'success', `Some instances could not be updated!`);
                    stopFlag = false;
                    console.log(error);
                }
            )
        })
        if(!startFlag) {
            Notification.showNotification('top', 'right', 'danger', 'AWS: Some error in starting instances!');
        }
        if(!stopFlag) {
            Notification.showNotification('top', 'right', 'danger', 'AWS: Some error in stopping instances!');
        }
        // if(startFlag && startFlag) {
        this.userService.updateUserInstances(body)
        .subscribe(
            res => {
                Notification.showNotification('top', 'right', 'success', 'Database: Records upated successfully!');
                this.ngOnInit();
            },
            error => {
                console.log('Error occured: '+error)
                Notification.showNotification('top', 'right', 'danger', 'Some error in updating instances!');
            }
        )
        // }
    }
    
    updateCustomers(form: NgForm, manager: any) {
        let updatedCustomers:any = [];
        for(let i = 0; i < this.customersInManager.length; i++) {
            if(this.customersInManager[i].email !== "" && this.customersInManager[i].email !== null && this.customersInManager[i].email !== undefined) {
                updatedCustomers.push(this.customersInManager[i].email);
            }
        }
        var body = {
            managerEmail: manager.email,
            customers: updatedCustomers
        }
        this.userService.updateManagerCustomers(body)
        .subscribe(
            res => {
                console.log(res);
                Notification.showNotification('top', 'right', 'success', 'Record upated successfully!');
                this.ngOnInit();
            },
            error => {
                console.log('Error occured: '+error)
                Notification.showNotification('top', 'right', 'danger', 'Some error in updating instances!');
            }
        )   
    }
    
    addNewInstance() {
        var last = this.user.instances[this.user.instances.length-1];
        if(last != undefined && ((last.instanceId == undefined || last.instanceId == ""))) return;
        this.user.instances.push({instanceId: '', role: '', region: '', state: 'stop'});
    }
    
    addNewCustomer() {
        var last = this.customersInManager[this.customersInManager.length-1];
        if(last != undefined && ((last.fullName == undefined || last.fullName == "") || (last.email == undefined || last.email == ""))) return;
        this.customersInManager.push({fullName: '', email: ''});
    }
    
    findCustomerByEmail(email) {
        if(!email) return;
        if(email.includes('.com')) {
            console.log(this.numberOfCustomers);
            for(let i = 0; i < this.numberOfCustomers; i++) {
                if(email == this.customers[i].email){
                    return this.customers[i].fullName;
                }
            }
        }
    }
    
    findCustomerByName(fullName) {
        if(!fullName) return;
        if(fullName.length > 3) {
            for(let i = 0; i < this.numberOfCustomers; i++) {
                if(this.customers[i].fullName.includes(fullName)){
                    // console.log(this.customers[i].email);
                    return this.customers[i].email;
                }
            }
        }
    }
    
    // deleteInstance(user: any, instanceId: string) {
    //     console.log(this.customers.indexOf(user));
    //     // console.log(user);
    //     console.log(instanceId);
    //     instanceId = "";
    //     user.role = "";
    //     console.log(user);    
    //     // this.customers.splice(this.customers.indexOf(id), 1);
    // }
    
    // removeInstance(user: any, instance: any) {
    //     this.user.instances.splice(this.instances.indexOf(instance));
    // }
    
    open(content, user) {
        this._id = user._id;
        this.user = user;
        // console.log(user);
        // console.log(this.user);
        if(Object.keys(content._def.references).includes('cardDetails')) {
            this.customersInManager = [];
            for(let i = 0; i < user.customers.length; i++) {
                for (let j = 0; j < this.customers.length; j++) {
                    // console.log(user.customers[i] + " |" + this.customers[i].email + " |"+this.customers.length);
                    if(user.customers[i] == this.customers[j].email){
                        this.customersInManager.push({fullName: this.customers[i].fullName, email: this.customers[i].email});
                    }
                }
            }
            // console.log(this.customersInManager);
            // console.log(user);
        }
        // this.instances = customer.instances;
        
        this.modalService.open(content, { size: 'lg' }).result.then((result) => {
            this.closeResult = `Closed with: ${result}`;
        }, (reason) => {
            this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
        });
    }
    
    private getDismissReason(reason: any): string {
        if (reason === ModalDismissReasons.ESC) {
            return 'by pressing ESC';
        } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
            return 'by clicking on a backdrop';
        } else {
            return  `with: ${reason}`;
        }
    }
    
}



